body{
  background-color: #010102;
}
.App{
  background-color: #af0e0e;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
  
  
}

#to-do-form input{
  background-color: rgba(27, 112, 137, 1);
  border: 0; 
  color: #fff;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(224, 172, 0); 
  cursor: pointer;
  color: #ebdfdf;
  
}
#to-do-form button:focus{
  outline: none;
}